import { graphql, Link } from "gatsby";
import React from "react";
import Layout from "../components/Layout";
import * as styles from '../styles/product.module.css';

export default function Product({ data }) {

  const product = data.contentfulProduct;

  React.useEffect(() => {
    document.getElementById("burger").style.backgroundColor = product.backgroundColor;
    document.getElementById("nav").style.backgroundColor = product.backgroundColor;
  });

  return (
    <Layout>
      <div className={styles.Product} >

        <div className={styles.textContainer}>
          <h1 className={styles.productName}>{product.name}</h1>
          <p>{product.longDescription.longDescription}</p>
          {/* <p>{product.teaser}</p> */}
          <p>{product.ingredients.ingredients}</p>
          <p>{product.analysis.analysis}</p>
          <Link to="/contact" className={styles.button}>LEARN MORE</Link>
        </div>

        <img src={product.packagePhoto.url} alt="product photo" className={styles.productImage}/>

        <div className={styles.imageBackground} style={{backgroundColor: product.backgroundColor}}></div>

        <div className={styles.icons}>
          <div className={styles.icon}>
            <img src={product.icon1.url} alt="ingredients 1" className={styles.iconImage}/>
            <p className={styles.iconText}>{product.icon1Text}</p>
          </div>
          <div className={styles.icon}>
            <img src={product.icon2.url} alt="ingredients 2" className={styles.iconImage}/>
            <p className={styles.iconText}>{product.icon2Text}</p>
          </div>
          <div className={styles.icon}>
            <img src={product.icon3.url} alt="ingredients 3" className={styles.iconImage}/>
            <p className={styles.iconText}>{product.icon3Text}</p>
          </div>
          <div className={styles.icon}>
            <img src={product.icon4.url} alt="ingredients 4" className={styles.iconImage}/>
            <p className={styles.iconText}>{product.icon4Text}</p>
          </div>
          <div className={styles.icon}>
            <img src={product.icon5.url} alt="ingredients 5" className={styles.iconImage}/>
            <p className={styles.iconText}>{product.icon5Text}</p>
          </div>
          <div className={styles.icon}>
            <img src={product.icon6.url} alt="ingredients 6" className={styles.iconImage}/>
            <p className={styles.iconText}>{product.icon6Text}</p>
          </div>
          {product.icon8 !== null ? 
            <div className={styles.icon}>
              <img src={product.icon7.url} alt="ingredients 7" className={styles.iconImage}/>
              <p className={styles.iconText}>{product.icon7Text}</p>
            </div> 
          : null}
          {product.icon8 !== null ? 
            <div className={styles.icon}>
              <img src={product.icon8.url} alt="ingredients 8" className={styles.iconImage}/>
              <p className={styles.iconText}>{product.icon8Text}</p>
            </div> 
          : null}
        </div>


      </div>
    </Layout>
  )
}

export const query = graphql`
query MyQuery($slug: String) {
  contentfulProduct(slug: {eq: $slug}) {
    id
    name
    teaser
    ingredients {
      ingredients
    }
    analysis {
      analysis
    }
    longDescription {
      longDescription
    }
    slug
    packagePhoto {
      url
    }
    backgroundColor
    icon1 {
      url
    }
    icon1Text
    icon2 {
      url
    }
    icon2Text 
    icon3 {
      url
    }
    icon3Text 
    icon4 {
      url
    }
    icon4Text 
    icon5 {
      url
    }
    icon5Text 
    icon6 {
      url
    }
    icon6Text 
    icon7 {
      url
    }
    icon7Text 
    icon8 {
      url
    }
    icon8Text 
  }
}
`
;